*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', sans-serif;
    text-decoration: none;
    text-align: center;
}

body{
    position: relative;
    height: 100vh;
}

nav{
    background-color: #150143e3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    padding: 10px 50px; /* Updated padding */
    box-shadow: 10px 10px 10px rgba(128, 128, 128, 0.548);
    color: white;
}
nav ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 50px;
}

li{
    width: 70px;

}

button:hover {
    background-color: #0f5ef1; 
    color: white; 
}

.hide{
    position: absolute;
    top: 70px;
}
